.enter {
    opacity: 0;
    transform: translateX(-200%);
}

.enterActive {
    opacity: 1;
    transform: translateX(0);
    transition: opacity 250ms linear, transform 250ms linear;
}

.exit {
    opacity: 1;
    transform: translateX(0);
}

.exitActive {
    opacity: 0;
    transform: translateX(-200%);
    transition: opacity 250ms linear, transform 250ms linear;
}

